*{margin:0;padding:0}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden;background-color:#c9dbe9}

div.weatherContainer{margin:100px auto 0px auto;display:block;width:320px;height:330px;border-radius:2px;box-shadow:0px 0px 18px 7px rgba(255,255,255,0.41)}div.weatherContainer .city{position:relative;color:#343434;font-size:2em;width:92%;padding:4%;display:block;float:left;background:#fefefe}div.weatherContainer .text{position:absolute;font-size:5em;bottom:0px;left:10px;z-index:3}div.weatherContainer .text:after{content:'Â°C';font-size:30px;float:right;position:relative;top:12px}div.weatherContainer .card{padding:10%;width:80%;height:200px;position:relative;z-index:0}div.weatherContainer .day{background:#bbdefb;color:#343434}div.weatherContainer .night{background:#2c3e50;color:#ababab}div.weatherContainer .rainyday{background:#596e78;color:#ababab}div.weatherContainer .rainynight{background:#5d86a4;color:#ababab}div.weatherContainer .stormday{-webkit-animation:lightning 5s ease infinite;animation:lightning 5s ease infinite;color:#ababab}div.weatherContainer .cloudy{height:100%;display:block}div.weatherContainer .cloudy .moon{left:130px;top:-100px}div.weatherContainer .cloud{display:block;background:#fafafa;position:relative;margin-top:80px;width:250px;height:100px;border-radius:50px;-webkit-animation:reveal 1s ease, slide 1s ease;animation:reveal 1s ease, slide 1s ease}div.weatherContainer .cloud:before{content:"";background:#fafafa;width:150px;height:150px;border-radius:50%;position:absolute;z-index:-1;left:30px;top:-60px;-webkit-animation:reveal 0s ease, slide 1s ease;animation:reveal 0s ease, slide 1s ease}div.weatherContainer .sol,div.weatherContainer .moon{background:#ffe082;width:130px;height:130px;border-radius:50%;position:absolute;top:30px;left:140px;z-index:-2;-webkit-animation:slide 1.5s ease-out 1s, reveal 1.5s ease both 1s;animation:slide 1.5s ease-out 1s, reveal 1.5s ease both 1s}div.weatherContainer .moon{background:#eaeaea}div.weatherContainer .moon:before{content:'';left:30px;background:#2c3e51;width:120px;height:120px;display:block;border-radius:50%;position:absolute;top:-30px}div.weatherContainer .clear{display:none;height:100%}div.weatherContainer .clear .sol,div.weatherContainer .clear .moon{position:relative;margin-right:0;top:35px;left:60px}div.weatherContainer .shine,div.weatherContainer .rain{width:40px;height:10px;background:#ffe082;position:absolute;border-radius:5px;-webkit-transform:rotate(90deg);transform:rotate(90deg);left:45px;top:-31px;-webkit-animation:reveal 2s ease 1.5s both;animation:reveal 2s ease 1.5s both}div.weatherContainer .shine:nth-child(1),div.weatherContainer .rain:nth-child(1){left:95px;-webkit-transform:rotate(120deg);transform:rotate(120deg);top:-17px;-webkit-animation:reveal 2s ease 2s both;animation:reveal 2s ease 2s both}div.weatherContainer .shine:nth-child(2),div.weatherContainer .rain:nth-child(2){left:130px;-webkit-transform:rotate(160deg);transform:rotate(160deg);top:20px;-webkit-animation:reveal 2s ease 2.5s both;animation:reveal 2s ease 2.5s both}div.weatherContainer .rain{background:#89C4F4;-webkit-transform:rotate(110deg);transform:rotate(110deg);top:130px;left:30px;-webkit-animation:reveal 2s ease 1.5s both, sparkle 1s ease 2.5s infinite;animation:reveal 2s ease 1.5s both, sparkle 1s ease 2.5s infinite}div.weatherContainer .rain:nth-child(1){-webkit-transform:rotate(110deg);transform:rotate(110deg);left:80px;top:130px;-webkit-animation:reveal 2s ease 2s both, sparkle 1s ease 3s infinite;animation:reveal 2s ease 2s both, sparkle 1s ease 3s infinite}div.weatherContainer .rain:nth-child(2){-webkit-transform:rotate(110deg);transform:rotate(110deg);left:130px;top:130px;-webkit-animation:reveal 2s ease 2.5s both, sparkle 1s ease 3.5s infinite;animation:reveal 2s ease 2.5s both, sparkle 1s ease 3.5s infinite}div.weatherContainer .rain:nth-child(3){left:180px;-webkit-animation:reveal 2s ease 3s both, sparkle 1s ease 4s infinite;animation:reveal 2s ease 3s both, sparkle 1s ease 4s infinite}div.weatherContainer .rainy{height:100%;display:none}div.weatherContainer .rainy .cloud{margin-top:40px;background:#ABB7B7}div.weatherContainer .rainy .cloud:before{background:#ABB7B7}div.weatherContainer .storm{display:none}div.weatherContainer .storm .cloud{margin-top:40px;background:#657171}div.weatherContainer .storm .cloud:before{background:#657171}div.weatherContainer .hazeday{background:#bbc4c8}div.weatherContainer .haze{display:none}div.weatherContainer .haze .cloud{margin-top:70px}@-webkit-keyframes reveal{from{opacity:0}to{opacity:1}}@keyframes reveal{from{opacity:0}to{opacity:1}}@-webkit-keyframes sparkle{0%,100%{opacity:1}50%{opacity:0.7}}@keyframes sparkle{0%,100%{opacity:1}50%{opacity:0.7}}@-webkit-keyframes slide{0%{margin-left:-10px}100%{margin-left:0px}}@keyframes slide{0%{margin-left:-10px}100%{margin-left:0px}}@-webkit-keyframes lightning{0%,35%,50%,70%,100%{background:#3b505a}45%,55%{background:#637882}}@keyframes lightning{0%,35%,50%,70%,100%{background:#3b505a}45%,55%{background:#637882}}@media screen and (max-width: 340px){.container{width:100%}}

#clouds{padding:100px 0;background:#c9dbe9;background:-linear-gradient(top, #c9dbe9 0%, #fff 100%);background:-moz-linear-gradient(top, #c9dbe9 0%, #fff 100%)}#clouds .cloud{width:200px;height:60px;background:#fff;border-radius:200px;-moz-border-radius:200px;-webkit-border-radius:200px;position:relative}#clouds .cloud:before,#clouds .cloud:after{content:'';position:absolute;background:#fff;width:100px;height:80px;position:absolute;top:-15px;left:10px;border-radius:100px;-moz-border-radius:100px;-webkit-border-radius:100px;-webkit-transform:rotate(30deg);transform:rotate(30deg);-moz-transform:rotate(30deg)}#clouds .cloud:after{width:120px;height:120px;top:-55px;left:auto;right:15px}#clouds .x1{-webkit-animation:moveclouds 15s linear infinite;animation:moveclouds 15s linear infinite}#clouds .x2{left:200px;-webkit-transform:scale(0.6);transform:scale(0.6);opacity:0.6;-webkit-animation:moveclouds 25s linear infinite;animation:moveclouds 25s linear infinite}#clouds .x3{left:-250px;top:-200px;-webkit-transform:scale(0.8);transform:scale(0.8);opacity:0.8;-webkit-animation:moveclouds 20s linear infinite;animation:moveclouds 20s linear infinite}#clouds .x4{left:470px;top:-250px;-webkit-transform:scale(0.75);transform:scale(0.75);opacity:0.75;-webkit-animation:moveclouds 18s linear infinite;animation:moveclouds 18s linear infinite}#clouds .x5{left:-150px;top:-150px;-webkit-transform:scale(0.8);transform:scale(0.8);opacity:0.8;-webkit-animation:moveclouds 20s linear infinite;animation:moveclouds 20s linear infinite}@-webkit-keyframes moveclouds{0%{margin-left:1000px}100%{margin-left:-1000px}}@keyframes moveclouds{0%{margin-left:1000px}100%{margin-left:-1000px}}

.wrapper{display:flex;justify-content:center;align-items:center;top:120px;left:0;right:0;z-index:6;position:absolute}.wrapper .main{height:90vh;background:#fff;border-radius:20px;box-shadow:0px 13px 40px -13px rgba(0,0,0,0.75);width:80%;margin:25px auto}.wrapper .main .container{width:100%}.wrapper .main .container .row .title-container{height:15vh;min-height:100%;background-size:cover;display:flex;justify-content:center;text-align:center;color:#000}.wrapper .main .container .row .title-container div h1{margin-top:25px;font-size:40px;letter-spacing:2px;line-height:1.3}.wrapper .main .container .row .title-container div h3{margin-top:5px;font-style:italic;font-weight:100;letter-spacing:1px;line-height:1.5}.wrapper .main .container .row .form-container{background-color:#2c3e50;height:70vh;padding-top:50px;padding-left:50px}.wrapper .main .container .row .form-container form{margin-top:50px}.wrapper .main .container .row .form-container form input[type="text"]{background-color:transparent;border:0;border-bottom:solid 1px #f16051;width:30%;padding-bottom:4px;color:#fff !important;font-weight:lighter;letter-spacing:2px;margin-bottom:30px;margin-right:20px;font-size:20px;outline:none}.wrapper .main .container .row .form-container form input[type="text"]:-webkit-autofill{-webkit-box-shadow:0 0 0 30px #2c3e50 inset;-webkit-text-fill-color:#fff !important}.wrapper .main .container .row .form-container form button.form-button{border:0;padding:8px 20px;margin:0 2px;border-radius:2px;font-weight:lighter;letter-spacing:1px;font-size:15px;cursor:pointer;background-color:#f16051;color:#fff;font-weight:100}.wrapper .main .container .row .form-container form button.form-button:active{outline:none}.wrapper .main .container .row .form-container form button.form-button:hover{cursor:pointer}.wrapper .main .container .row .form-container div.weatherInfo{width:60%;font-size:20px;font-weight:200;letter-spacing:2px}.weatherKey{color:#f16051;border-bottom:solid 2px rgba(255,255,255,0.06);padding:20px 0 20px 0;font-weight:400}.weatherKey:last-child{border:0}.weatherValue{color:#fff;font-weight:200}.weatherError{color:#f16051;font-size:20px;letter-spacing:1px;font-weight:200}

